.main-content {
  display: flex;
  justify-content: center;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    max-width: 1024px;
    padding: 50px 40px;
  }

  
  &__card {
    margin-right: 48px;
    margin-bottom: 46px;

    &:nth-child(4n) {
      margin-right: 0;
    }
  
    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }
}
